<template>
  <div class="page-container">
    <!-- 连接按钮 -->
    <div class="button">
      <button @click="walletconnection">{{ wallet_connection ? 'Disconnect' : 'Connect' }}</button>
      <a class="default-font">{{ wallet_connection ? wallet_address : '' }}</a>
      <a class="default-font">{{ wallet_connection ? '  Balance: ' : '' }}{{ wallet_connection ? kaspa_balance : '' }}</a>
      <button @click="swtichnetwork">{{ network ? 'mainnet' : 'testnet' }}</button>
    </div>
    
    <!-- 添加搜索框 -->
    <div class="search-box">
      <input v-model="searchQuery" type="text" placeholder="token name..." />
    </div>

    <p></p>

    <table class="data-table">
      <thead>
        <tr>
          <th>编号</th>
          <th>名称</th>
          <th>价格</th>
          <th>近24小时成交量</th>
          <th>总成交量</th>
          <th>市场估值</th>
          <th>持有者数量</th>
        </tr>
      </thead>
      <tbody>
        <!-- 根据当前显示的数据行数来渲染数据 -->
        <tr v-for="(item, index) in visibleData" :key="index" class="table-row" @click="goToMyToken(item)">
           <td><img :src="getIconSrc(item.tick)"  alt="icon" width="40" height="40" class="rounded-icon"></td>
            <td>{{ item.tick }}</td>
            <td><span class="mirrored">K </span>{{ item.price }}</td>
            <td><span class="mirrored">K </span>{{ item.volume_24h }}</td>
            <td><span class="mirrored">K </span>{{ item.volume_total }}</td>
            <td><span class="mirrored">K </span>{{ item.price * item.max /100000000}}</td>
            <td>{{ item.holderTotal }}</td>

        </tr>
      </tbody>
    </table>

    <!-- 在表格下方居中显示按钮 -->
    <div class="toggle-button-container">
      <button @click="toggleDataDisplay">{{ showAllData ? '显示前20行' : '显示所有数据' }}</button>
      <el-backtop :right="200" :bottom="100" />
    </div>

    <div>
        <p>
          <a>Telegram</a>
          <a>X</a>
          <a>Discord</a>
        </p>
    </div>
  </div>
  <div class="page-container">
  </div>
</template>





<script setup>
import { ElMessage } from 'element-plus';
import { fetchData,url_address  } from './js/commu';
</script>

<script>
export default {
   name: "App",
   data() {
    return {
      // 填充30行示例数据
      index_data:[],
      pageTitle: "Kasporter.Atomic swap",
      showAllData: false, // 控制是否显示所有数据
      wallet_connection: false, // 控制连接按钮状态
      kaspa_network: '',
      wallet_address: '',
      kaspa_balance: 0,
      tokens_balance: null,
      searchQuery: '', // 用于存储搜索框的输入内容
      items:null,
      mainnet:true,
      defaultIcon: 'https://krc20-assets.kas.fyi/icons/KDAO.jpg',  // 默认图标
      iconUrls: {},
      network: true,
    };
  },
  computed: {
  // 根据 showAllData 控制要显示的数据行数
  visibleData() {
    const filteredData = this.filteredData;
    return this.showAllData ? filteredData : filteredData.slice(0, 15);
  },

  // 计算属性：过滤数据
  filteredData() {
    // 确保 this.index_data 是一个数组
    if (!Array.isArray(this.index_data)) {
      console.error('index_data is not an array:', this.index_data);
      return []; // 如果 index_data 不是数组，返回空数组
    }

    // 获取搜索关键字，并确保它是一个有效的字符串
    const query = this.searchQuery ? this.searchQuery.toLowerCase() : '';

    // 过滤数据
    const filtered = this.index_data.filter(item => {
      // 确保 item 和 item.tick 是有效的且为字符串
      if (item && item.tick && typeof item.tick === 'string') {
        return item.tick.toLowerCase().includes(query);
      } else {
        return false;
      }
    });

    // 根据 holderTotal 对数据进行降序排序
    return filtered.sort((a, b) => {
    // 确保 volume_total 和 holderTotal 是数字，进行排序
    const volumeTotalA = a.volume_total ? a.volume_total : 0;
      const volumeTotalB = b.volume_total ? b.volume_total : 0;
      const holderTotalA = a.holderTotal ? a.holderTotal : 0;
      const holderTotalB = b.holderTotal ? b.holderTotal : 0;

      // 先按 volume_total 降序排序
      if (volumeTotalB !== volumeTotalA) {
        return volumeTotalB - volumeTotalA;
      }
      // 如果 volume_total 相等，再按 holderTotal 降序排序
      return holderTotalB - holderTotalA;
    });
  }
},


  methods: {
    // 切换数据行显示的数量
    toggleDataDisplay() {
      this.showAllData = !this.showAllData;
    },



     getIconSrc(tick) {
      const iconUrl = "https://krc20-assets.kas.fyi/icons/"+tick+".jpg";
                     fetch(iconUrl).then((response) => {
                        if (response.ok) {
                          this.iconUrls[tick] = iconUrl;
                        } else {
                          this.iconUrls[tick] = this.defaultIcon;
                        }
                      }).catch((error) => {
                        console.error('Error fetching icon:', error);
                        this.iconUrls[tick] = this.defaultIcon;
                      });
                      return this.iconUrls[tick];
 
  },

   

    scrollToBottom() {
      window.scrollTo({
        top: document.body.scrollHeight,
        behavior: 'smooth' // 平滑滚动
      });
    },

    // 切换钱包连接状态
    async walletconnection() {
      if (this.wallet_connection) {
        // 断开钱包连接
        if (window.kasware) {
          await window.kasware.disconnect(window.location.origin);
          localStorage.removeItem('wallet_address');
          localStorage.removeItem('kaspa_balance');
          localStorage.removeItem('wallet_connection');
          this.wallet_connection = !this.wallet_connection;
        }
      } else {
        // 连接钱包
        if (window.kasware) {
          try {
            let accounts = await window.kasware.requestAccounts();
            this.wallet_address = accounts[0];
            if (accounts[0].length > 0) {
              this.kaspa_network = await window.kasware.getNetwork();
              let ret = await window.kasware.getBalance();
              this.kaspa_balance = (ret.total / 100000000).toFixed(4);
              this.wallet_connection = !this.wallet_connection;
              window.kasware.getKRC20Balance().then((tokens_balance) => {
                this.tokens_balance = tokens_balance;
                localStorage.setItem('tokens_balance', JSON.stringify(this.tokens_balance));
              });
              localStorage.setItem('wallet_address', this.wallet_address);
              localStorage.setItem('kaspa_balance', this.kaspa_balance);
              localStorage.setItem('wallet_connection', true);
            }
          } catch (e) {
            console.log("error:", e);
          }
        }
      }
    },


    async swtichnetwork(){
     this.mainnet = !this.mainnet;
     localStorage.setItem('mainnet', this.mainnet);
     try {
  let res = await window.kasware.switchNetwork(this.mainnet? "kaspa_mainnet" : "kaspa_testnet_10");
      this.network = !this.network;
      if (window.kasware) {
          await window.kasware.disconnect(window.location.origin);
          localStorage.removeItem('wallet_address');
          localStorage.removeItem('kaspa_balance');
          localStorage.removeItem('wallet_connection');
          this.wallet_connection = !this.wallet_connection;
        }
  console.log(res)
} catch (e) {
  console.log(e);
}
    },

    showMessage() {
      ElMessage.error('Oops, Please connect your wallet.');
    },

    goToMyToken(item) {
      if (!this.wallet_connection) {
        this.showMessage();
        return;
      }
      const id = item.id; // 获取要传递的 ID

      localStorage.setItem('items', JSON.stringify(item));
      this.$router.push({ name: 'MyToken', params: { id } });
      return;
    },
  

   
    async fetch_tokens_index(url) {
      const data = await fetchData(url);

if (data) {
  console.log('Received Data:', data);
  return data;
} else {
  console.log('No data received or an error occurred.');
}
}


  },

  created(){
    
    const url = url_address+"/get_token_list";
    fetchData(url).then(response => {
    // 假设 response.data 是 JSON 字符串，解析它
    this.index_data = JSON.parse(response.data);
    localStorage.setItem('Index_data', response.data);
    console.log('获取数据成功:');
  }).catch(error => {
    console.error('获取数据时出错:', error);
  });
    
  } ,
  mounted() {
    //获取所有token的索引信息

    document.title = this.pageTitle; // 设置页面标题
    this.index_data = JSON.parse(localStorage.getItem('Index_data'));
    // 读取保存的连接状态
    const savedtokens_balance = localStorage.getItem('tokens_balance');
    const mainnet = localStorage.getItem('mainnet');
    const savedWalletAddress = localStorage.getItem('wallet_address');
    const savedKaspaBalance = localStorage.getItem('kaspa_balance');
    const savedWalletConnection = localStorage.getItem('wallet_connection') === 'true';
    if (savedWalletConnection) {
      this.wallet_address = savedWalletAddress;
      this.kaspa_balance = savedKaspaBalance;
      this.wallet_connection = savedWalletConnection;
      this.tokens_balance = savedtokens_balance;
      this.mainnet = mainnet;
    }
  }
};
</script>


<style scoped>
@import './default.css';
</style>
