// src/router.js
import { createRouter, createWebHistory } from 'vue-router';
import MyHome from './MyHome.vue';  // 主页
import MyToken from './MyToken.vue';  // token详情页面
import MyIndex from './MyIndex.vue';  // tokens索引页面
import MyOrders from './MyOrders.vue';  // 我的挂单记录页面
import MyWallet from './MyWallet.vue';  // 我的挂单记录页面
import MySell from './MySell.vue';  // 我的挂单记录页面

const routes = [
  {
    path: '/MyHome',
    name: 'MyHome',
    component: MyHome,
  },
  {
    path: '/',
    name: 'MyIndex',
    component: MyIndex,
  },
  {
    path: '/Token/:id?',
    name: 'MyToken',
    component: MyToken,
    meta: { requiresWallet: true }, // 需要钱包连接
  },
  {
    path: '/Orders',
    name: 'MyOrders',
    component: MyOrders,
    meta: { requiresWallet: true }, // 需要钱包连接
  },
  {
    path: '/MyWallet',
    name: 'MyWallet',
    component: MyWallet,
    meta: { requiresWallet: true }, // 需要钱包连接
  },
  {
    path: '/MySell/:token?',
    name: 'MySell',
    component: MySell,
    meta: { requiresWallet: true }, // 需要钱包连接
  },
];

const router = createRouter({
  history: createWebHistory(), // 使用 HTML5 History 模式
  routes, // 路由配置
});

export default router;
