export function msgnotifi(msg ,type){
    if(type=="error"){
        ElMessage.error(msg)
    }else{
    ElMessage({message: msg,type: type,});
    }
}


// 手动清除加载状态
export function closeLoading(loadingInstance) {
  if (loadingInstance) {
    loadingInstance.close();  // 关闭全屏加载
    loadingInstance = null;   // 清空加载实例
  } else {
    console.warn('Loading is not active.');
  }
}

export function loadingFullScreen() {  
  // 全屏加载
  const loadingInstance = ElLoading.service({
    lock: true,
    text: 'Loading...',
    background: 'rgba(0, 0, 0, 0.7)',
  });

  return loadingInstance;
}



export function initChart(klineData) {
    // 获取 DOM 容器
    const chart = echarts.init(this.$refs.chart);
    
    // 配置图表选项
    const option = {
      backgroundColor: '#555', // 设置背景颜色为灰色
      title: {
        text: ' ',
        left: 'center',
      },
      tooltip: {
        trigger: 'axis',
        formatter: function (params) {
          const param = params[0];
          return `
            日期: ${param.name}<br/>
            开盘价: ${param.data[1]}<br/>
            最高价: ${param.data[2]}<br/>
            最低价: ${param.data[3]}<br/>
            收盘价: ${param.data[4]}
          `;
        },
      },
      grid: {
        left: '10%',
        right: '10%',
        bottom: '10%',
        top: '20%',
      },
      xAxis: {
        type: 'category',
        boundaryGap: false,
        data: klineData.map(item => item[0]), // 日期
      },
      yAxis: {
        scale: true,
        splitArea: {
          show: true,
        },
      },
      series: [
        {
          name: 'K线图',
          type: 'candlestick',
          data: klineData.map(item => item.slice(1)), // 取出每个K线的 OHLC（开盘、最高、最低、收盘价）
          itemStyle: {
            color: '#00da3c',  // 收盘价高于开盘价时为绿色
            color0: '#ec0000', // 收盘价低于开盘价时为红色
            borderColor: '#00da3c',
            borderColor0: '#ec0000',
          },
          emphasis: {
            itemStyle: {
              //color: '#31c27c',
              borderColor: '#555',
              borderWidth: 1,
            },
          },

        },
      ],
    };

    // 设置图表选项
    chart.setOption(option);
  }

export async function POST_Request(path, jsondata) {
    try {
      const response = await fetch(path, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(jsondata)
      });
      const data = await response.json();
      if (data) {
        // 可以在这里处理返回的数据
        //console.log("Data received:", data.data);
      return data.data;
      }
    } catch (e) {
      console.error("Error fetching token price:", e);
      return 0;
    }
  }
  
  export function handle_order(g_id) {
    return new Promise((resolve, reject) => {
      const gid = { id: g_id };
      const get_txjsonstring = url_address + '/get_txjsonstring';
      
      // 异步获取交易 JSON 字符串
      POST_Request(get_txjsonstring, gid).then(result => {
        if (!result) {
          reject('requestion json string failure');
          return;
        }
  
        const params = {
          txJsonString: result,
          extraOutput: [
            //{ address: g_gasfee_addr, amount: 1 },
          ],
          priorityFee: 0,
        };
        console.log("result:",result);
        // 调用 buyKRC20Token 函数进行购买
        window.kasware.getNetwork().then(res=>{
             console.log("network:",res)
        }).catch(e=>{

          console.log("network error:",e)
        });
        

        window.kasware.buyKRC20Token(params).then(txid => {
          if (txid) {
            console.log("buy succeeded:", txid);
  
            // 回写数据，添加 txid 并将 isSelled 设置为 true
            const set_isSelled_true = url_address + '/set_isSelled_true';
            POST_Request(set_isSelled_true, gid).then(result => {
              // 判断回写数据是否成功
              if (result) {
                console.log("buy token success:");
                resolve('transaction is succeeded');
              } else {
                reject('Failed to update isSelled status');
              }
            }).catch(e => {
              console.log("Error in setting isSelled status:", e);
              reject('Failed to set isSelled status');
            });
          } else {
            reject('Failed to buy token');
          }
        }).catch(e => {
          console.log("buy token failed:", e);
          reject('buy token failed: ' + e.message);
        });
      }).catch(e => {
        console.log("Failed to get txJsonString:", e);
        reject('Failed to get txJsonString');
      });
    });
  }
  

  export function sleep(ms) {
    return new Promise(resolve => {
      const start = performance.now();
      const end = start + ms;
  
      function checkTime() {
        if (performance.now() >= end) {
          resolve();
        } else {
          requestAnimationFrame(checkTime);  // 高精度延时
        }
      }
  
      checkTime();
    });
  }
  

  export function set_psktExtraOutput(account_json, servicefee) {
    let psktExtraOutput = [];
  
    if (Array.isArray(account_json)) {
      // 如果 account_json 是一个数组，遍历并打印
      for (const number of account_json) {
        console.log(number.addr, number.percent);
        psktExtraOutput.push({ address: number.addr, amount: servicefee * number.percent / 100 });
      }
    } else {
      // 如果 account_json 不是数组，处理单个地址
      psktExtraOutput.push({ address: account_json, amount: servicefee });
    }
    return psktExtraOutput;
  }

