             <!-- 使用 v-for 渲染列表项 -->
             <td> <spawn>quantity</spawn> </td>
             <td> <spawn>price</spawn> </td>
            <td> <spawn>totalprice</spawn> </td>
            <td> <spawn>list</spawn> </td>
            <tr v-for="(item, index) in order_list" :key="index" class="table-row" @click="buy_token(item.id)">
               <td> {{ item.quantity }} </td>
               <td> <span class="mirrored">K </span>{{ item.price }} </td>
               <td>  <span class="mirrored">K </span>{{ item.totalkas }} </td>
