        ['2023-01-07', 140, 140, 125, 145],
        ['2023-01-07', 140, 140, 125, 145],
        ['2023-01-07', 140, 140, 125, 145],
        ['2023-01-07', 140, 140, 125, 145],
        ['2023-01-07', 140, 140, 125, 145],
        ['2023-01-07', 140, 140, 125, 145],
        ['2023-01-07', 140, 140, 125, 145],
        ['2023-01-07', 140, 140, 125, 145],
        ['2023-01-07', 140, 140, 125, 145],
        ['2023-01-07', 140, 140, 125, 145],
        // ['日期', 开盘价, 收盘, 最低价, 最高]
      ],
        order_list: [
        { amount: 10, price: 10 ,totalprice:100,txjsonstring:"test"},
        { amount: 10, price: 10 ,totalprice:100,txjsonstring:"test"},
        { amount: 10, price: 10 ,totalprice:100,txjsonstring:"test"},
        { amount: 10, price: 10 ,totalprice:100,txjsonstring:"test"},
        { amount: 10, price: 10 ,totalprice:100,txjsonstring:"test"},
        { amount: 10, price: 10 ,totalprice:100,txjsonstring:"test"},
        { amount: 10, price: 10 ,totalprice:100,txjsonstring:"test"},
        { amount: 10, price: 10 ,totalprice:100,txjsonstring:"test"},
        { amount: 10, price: 10 ,totalprice:100,txjsonstring:"test"},
        { amount: 10, price: 10 ,totalprice:100,txjsonstring:"test"}
      ],
      tokenId: null, // 用于存储传递的 ID
      tokens_balance:null,
      token_info: null,
      mainnet: true,
      showModal: false, // 控制模态框的显示与隐藏
      showModal_cancle: false, // 控制模态框的显示与隐藏
      userInput: '', // 用户输入的内容
      tokenAmount: 0, // Token数量
      tokenPrice: 0, // 单价
      savedWalletAddress:null,
      
    };
  },

  computed: {
    fullName() {
      return `${this.firstName} ${this.lastName}`;
    },


  },

  methods: {
// 发送 GET 请求
async fetchData(url) {
  fetch(url, { 
    method: 'GET',
    timeout: 3000 // 设置超时时间为 3000 毫秒
  })
  .then(response => {
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    return response.json();
  })
  .then(data => {
    console.log('Response Data:', data);
  })
  .catch(error => {
    if (error.name === 'AbortError') {
      console.error('Request timeout, please try again later.');
    } else {
      console.error('Error fetching data:', error);
    }
  });
},

async postData(url, data) {
  try {
    const controller = new AbortController();
    const timeout = setTimeout(() => controller.abort(), 3000); // 设置超时时间为 3000 毫秒

    const response = await fetch(url, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json', // 告诉服务器请求体是 JSON 格式
      },
      body: JSON.stringify(data), // 将数据转换为 JSON 字符串
      signal: controller.signal, // 绑定超时信号
    });

    clearTimeout(timeout); // 清除超时计时器

    // 检查响应状态
    if (!response.ok) {
      throw new Error(`HTTP error! Status: ${response.status}`);
    }

    // 处理空响应的情况
    const text = await response.text(); // 获取响应内容作为文本
    if (!text) {
      console.log('No content in response.');
      return null; // 或返回一个默认值
    }

    // 如果响应已经是一个对象，直接返回它
    let jsonData = text;
    try {
      jsonData = JSON.parse(text); // 尝试解析 JSON 字符串
    } catch (e) {
      // 如果解析失败，直接返回原始响应文本
      console.error('Failed to parse JSON:', e);
      jsonData = text;
    }

    console.log('Data posted:', jsonData);
    return jsonData;

  } catch (error) {
    if (error.name === 'AbortError') {
      console.error('Request timeout, please try again later.');
    } else {
      console.error('Error posting data:', error);
    }
    throw error; // 重新抛出错误以便调用者处理
  }
},





    initChart() {
      // 获取 DOM 容器
      const chart = echarts.init(this.$refs.chart);
      
      // 配置图表选项
      const option = {
        backgroundColor: '#555', // 设置背景颜色为灰色
        title: {
          text: ' ',
          left: 'center',
        },
        tooltip: {
          trigger: 'axis',
          formatter: function (params) {
            const param = params[0];
            return `
              日期: ${param.name}<br/>
              开盘价: ${param.data[1]}<br/>
              最高价: ${param.data[2]}<br/>
              最低价: ${param.data[3]}<br/>
              收盘价: ${param.data[4]}
            `;
          },
        },
        grid: {
          left: '10%',
          right: '10%',
          bottom: '10%',
          top: '20%',
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: this.klineData.map(item => item[0]), // 日期
        },
        yAxis: {
          scale: true,
          splitArea: {
            show: true,
          },
        },
        series: [
          {
            name: 'K线图',
            type: 'candlestick',
            data: this.klineData.map(item => item.slice(1)), // 取出每个K线的 OHLC（开盘、最高、最低、收盘价）
            itemStyle: {
              color: '#00da3c',  // 收盘价高于开盘价时为绿色
              color0: '#ec0000', // 收盘价低于开盘价时为红色
              borderColor: '#00da3c',
              borderColor0: '#ec0000',
            },
            emphasis: {
              itemStyle: {
                //color: '#31c27c',
                borderColor: '#555',
                borderWidth: 1,
              },
            },

          },
        ],
      };

      // 设置图表选项
      chart.setOption(option);
    },

    async buy_token(id) {  // 弹出消息
    console.log('Buy Token:', id);
    let loadingIns;  // 确保变量加载实例在函数内声明
    const trans_timeout=15000;  // 交易超时时间
    try {
        loadingIns = loadingFullScreen();  // 启动加载动画
        console.log('loadingIns:', loadingIns);

        // 创建一个 Promise 来模拟交易操作，并设置超时
        const transactionPromise = new Promise((resolve, reject) => {
        // 调用实际的 buytoken() 函数执行交易
        handle_order(id)
            .then(result => {
                // 交易成功时调用 resolve
                resolve(result);
            })
            .catch(error => {
                // 交易失败时调用 reject
                reject('交易失败: ' + error);
            });

            // 如果在指定时间内没有完成交易，视为超时，调用 reject
            setTimeout(() => {
                reject('TimeOut');
            }, trans_timeout);  // 设置最大超时为 15 秒
        });

        // 等待交易完成或超时
        const result = await transactionPromise;
        
        // 根据交易结果显示消息
        msgnotifi(result, 'success');  // 交易成功消息
        this.order_list = this.fetch_order_list(this.token_info.tick);
    } catch (e) {
        // 捕获超时或其他错误，显示失败消息
        msgnotifi('process transtion failured', 'error');
    } finally {
        // 无论成功或失败，关闭加载动画
        if (loadingIns) {
            closeLoading(loadingIns);  // 关闭加载动画
        }
    }
},




  fetch_order_list(token_name){
   
      const url = url_address+"/get_order_list?token="+token_name;
      console.log('fetch_order_list:', url,token_name);
    fetchData(url).then(response => {
    // 假设 response.data 是 JSON 字符串，解析它
    this.order_list = JSON.parse(response.data);
    localStorage.setItem('order_list', response.data);
    console.log('获取数据成功:',this.order_list);
  }).catch(error => {
    console.error('获取数据时出错:', error);
  });

   return this.order_list;
  },

  fetch_order_price_his(token_name){
    console.log('fetch_order_price_his:', token_name);
    return this.klineData;
},

   goto_mysell(){
    const token = this.token_info.tick; // 获取要传递的 ID
