   },

   submitInput() {
      // 提交用户输入的内容
      console.log('用户输入内容:', this.userInput);
      this.showModal = false; // 关闭模态框
    },

      // 设置数量为全部
      setAllAmount() {
        //this.token_info.tick
        if(!this.tokens_balance){
          return ;
        }
        this.tokens_balance.forEach((token, index) => {
          if(this.token_info.tick == token.tick){
                 console.log(`Index: ${index}, Value: ${token.tick},${token.price}`);
                 this.tokenAmount =token.balance;
                 this.tokenAmount /= 100000000;
          }
        });
      
    // 假设全部数量是100，您可以调整
      },
      FloorPrice(){
        this.tokens_balance.forEach((token) => {
          if(this.token_info.tick == token.tick){
                 this.tokenPrice =this.token_info.price;
                 console.log("Index: Price:" ,this.token_info.price);
          }
        });
      },
      // 提交订单
      async submitOrder() {
        if(this.isChecked){
          console.log("private",this.isChecked);
        }
        //alert(`订单提交成功！\n数量: ${this.tokenAmount} \n单价: ${this.tokenPrice.toFixed(2)} \n总价: ${(this.tokenAmount * this.tokenPrice).toFixed(2)}`);
        const total_price = (this.tokenAmount * this.tokenPrice).toFixed(2);
        console.log("atomic_order_token param:",this.token_info.tick,this.tokenAmount, total_price,3,1,this.savedWalletAddress,this.mainnet) ;

        if(total_price <5){
          console.log(total_price);
          msgnotifi("let Total Pirce more than 5 KAS","warning");
          return ;
        }
        //关掉下单窗口
        this.showModal = false; // 提交后关闭弹窗
        await atomic_order_token(this.token_info.tick,this.tokenAmount,total_price,3,1,this.savedWalletAddress,this.mainnet,this.isChecked),

        this.order_list = this.fetch_order_list(this.token_info.tick);
      },

      async cancleOrder(item){
        this.showModal_cancle = false;
         //取消订单
         const tickname = this.token_info.tick;
         const txid = item.inputs[0].transactionId;
         const param = {
            krc20Tick: tickname,
            sendCommitTxId: txid,
        };
         console.log("txid:",txid,tickname,param);
         try{
         const cancle_txid = await window.kasware.cancelKRC20Order(param);
        console.log("cancle txid:",cancle_txid,item.id);
      
        //插入iscancled

        try{
            const cparam ={
              id : item.id
            }
            console.log("token id:",item.id);
            const uri = url_address+'/set_iscancled_true';
            const rs = await this.postData(uri,cparam);
            //const data = JSON.parse(rs);
            console.log("ret:",rs);
            if(rs){
              console.log("set iscancled true success",);
              this.order_list = this.fetch_order_list(this.token_info.tick);
              msgnotifi("Cancle order success","success");
            }
            }catch(e){
              console.error("Error fetching orders:", e);
            }

           
        }catch (e){
            msgnotifi("Cancle order failured","warning");
        }
      },


      closeModal() {
        this.showModal = false;  // 关闭弹窗
        this.showModal_cancle = false;
      },


  },
  created() {
    this.tokenId = this.$route.params.id; // 从路由参数中获取 ID
    this.token_info = JSON.parse(localStorage.getItem('items'));
    console.log('Token ID:', this.tokenId,this.token_info);
    

  },

  mounted() {
    //读取数据,token详情,token历史价格,token在售订单
