    //获取当前token的订单,价格升序排列
    if (token_balance && Array.isArray(token_balance)) {
          this.tokens_balance=token_balance;
} else {
    console.log("tokens_balance 数据不可用或不是一个数组");
}
    this.savedWalletAddress = localStorage.getItem('wallet_address');
    this.mainnet = localStorage.getItem('mainnet');
    this.order_list = this.fetch_order_list(this.token_info.tick);        //获取已上架的token订单列表
     console.log("order_list",this.order_list);
    this.klineData = this.fetch_order_price_his(this.token_info.tick);    //获取token历史价格
